import { Component, OnInit, ViewChild, TemplateRef, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { VendorService } from 'src/app/api/controllers/Vendor';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';
import * as _ from 'underscore';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ActivatedRoute, Router } from '@angular/router';
import { ViewVendorReportComponent } from 'src/app/modules/ams/vendor/components/view-vendor-report/view-vendor-report.component';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { MatDrawer } from '@angular/material/sidenav';

@Component({
  selector: 'app-vendor-report-data',
  templateUrl: './vendor-report-data.component.html',
  styleUrls: ['./vendor-report-data.component.scss']
})
export class VendorReportDataComponent implements OnInit {
  pageName: string = "";
  pageDesp: string = "";
  
  vendorCategoryData:any;
  vendorCategoryId:string = "";

  vendorData:string = "";
  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  modalService:any;

  vendor:any;

  vendorListData:any;

  categoryName:string = "";

  selectedInput:string = "";
  columnField: any = {};


  isVendorLoaded: boolean = false;
  allVendorListData: any;
  reportsDataList:any;
  reportsTypes: any;
  reportsData:any;
  vendorList: any;
  unitBlocksData: any;
  selected_report_type: any;

  public columnData: any;
  public vendorTableList: Array<any> = [];

  @ViewChild("viewVendorRef", { static: false }) viewVendorRef: TemplateRef<any>;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  isDataLoaded: boolean = false;
  @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
  blockId = null;
    blockNo: string = "";
    end_date = null;
    start_date = null;

  filterSelected:string = "all";
	singleBlock:string = "Select Tower";
	selectedBlockId:number;

  constructor(
    public dialog: MatDialog,
    private vendorService:VendorService,
  	private lookupService:LookupService,
    private sessionService:SessionService,
    private router: Router,
    private route: ActivatedRoute,
    private apartmentService: ApartmentService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }

  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showVendorInfo(vendor){
    this.dialog.open(this.viewVendorRef, {
        panelClass: 'material',
        disableClose: true,
        data: vendor
    });

  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }


  
  selectReport(type){
    this.vendorList = []
    this.selected_report_type = type;
    if(type == 'Active'){
      this.vendorList = this.allVendorListData.filter(item => {
        return item.isActive;
      });
    }else{
      this.vendorList = this.allVendorListData.filter(item => {
        return (item.isActive == false);
      });
    }

    this.isDataLoaded = true;
    let arrangeVendorRawData = {
      localdata: this.vendorList,
      datatype: "array"
    }
    this.vendorTableList = new jqx.dataAdapter(arrangeVendorRawData); 

    this.totalItems = this.vendorList.length;

    if (this.totalItems > this.itemLimit) {
      this.ItemEndIndex = this.itemLimit;
    }
    else {
      this.ItemEndIndex = this.totalItems;
    }
  }

  getAllBlockData(){

	  this.filterSelected = 'all';
	  this.singleBlock = "Select Tower";
	  
	  this.blockId = "";
	  this.datagrid.clearfilters();
  
	}

	getSingleBlock(block){
  	
		this.filterSelected = 'single';
		this.singleBlock = block.apartmentBlockNumber;
		this.blockId = block.apartmentBlockId;

		let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = this.singleBlock;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
	  
			this.datagrid.showfiltercolumnbackground(false);
	  
			this.columnData.forEach(item => {
			  if(item.datafield != 'Actions') {
				this.datagrid.addfilter(item.datafield, filtergroup, true);
			  }
			})
	  
			this.datagrid.applyfilters();
	}

  onGlSearchFilter(event) {
		if(event != ""){

			let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = event;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
	  
			this.datagrid.showfiltercolumnbackground(false);
	  
			this.columnData.forEach(item => {
			  if(item.datafield != 'Actions') {
				this.datagrid.addfilter(item.datafield, filtergroup, true);
			  }
			})
	  
			this.datagrid.applyfilters();
			
		  }
		  else {
			this.datagrid.clearfilters();
		  }
	   }

  getPrintParams(event) {
    this.datagrid.exportdata(event, 'VendorReportData');
  }

  goBack() {
    this.matDrawer.close();
  }

filterApply() {
    this.goBack();
  
    let filtergroup = new jqx.filter();
    let filter_or_operator = 1;
    let filtervalue = event;
    let filtercondition = 'contains';
    let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
    filtergroup.operator = 'or';
    filtergroup.addfilter(filter_or_operator, filterData);
  
    this.datagrid.showfiltercolumnbackground(false);
  
    this.columnData.forEach(item => {
      if(item.datafield != 'Actions') {
      this.datagrid.addfilter(item.datafield, filtergroup, true);
      }
    })
  
    this.datagrid.applyfilters();
    
  }

  clearFilter() {
    this.blockId = null;
 this.start_date = null;
 this.end_date = null;
    // =this.getTicketByAdmin();
    this.datagrid.clearfilters();
    this.goBack();
  }

  @HostListener('window:onViewVendor', ['$event.detail'])
  onViewVendor(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    // this.dialog.open(this.viewVendorRef, {
    //   panelClass: 'material',
    //   disableClose: true,
    //   data: dataRecord
    // });
    const dialogRef = this.dialog.open(ViewVendorReportComponent, {
      width: '500px',
      data: dataRecord
    });

    dialogRef.afterClosed().subscribe(result => {

    });
  }

  ngOnInit() {
    this.pageName = this.route.params['value'].name;
    this.selected_report_type = this.pageName;

    let unitBlockParams = {
			apartmentId: this.sessionService.apartmentId
		}

		this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
			this.unitBlocksData = res;
		});


    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }

    this.columnData = [{
      text: 'Vendor ID',
      datafield: 'vendorId',
      minwidth: 80,
      maxwidth: 90,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, 
    {
      text: 'Vendor Name',
      datafield: 'vendorName',
      minwidth: 160,
      maxwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, 
    {
      text: 'Email ID',
      datafield: 'email',
      minwidth: 170,
      maxwidth: 200,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, 
    {
      text: 'Vendor Category',
      datafield: 'vendorCategoryId',
      minwidth: 140,
      maxwidth: 160,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, 
    {
      text: 'Vendor Type',
      datafield: 'vendorTypeId',
      minwidth: 100,
      maxwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, 
    {
      text: 'Contact Person',
      datafield: 'contactPerson',
      minwidth: 130,
      maxwidth: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Phone No',
      datafield: 'phone1',
      minwidth: 110,
      maxwidth: 130,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'VAT',
      datafield: 'vatorTin',
      minwidth: 130,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Percentage Tax',
      datafield: 'tax1',
      minwidth: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'With Holding Tax',
      datafield: 'tax2',
      minwidth: 120,
      maxwidth: 140,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      minwidth: 120,
      cellclassname: 'action-cell',
      cellsrenderer: (row : number, column, value): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="viewVendor(' + row + ')"><i class="fa fa-eye icon view" aria-hidden="true"></i></div>'
      },
      renderer: columnrenderer
    }];

    this.vendor = {};
    this.vendorCategoryId = "All";
    this.vendor.categoryName = "";


  let params = {
    apartmentId: this.sessionService.apartmentId,
    active: this.pageName === "Active" ? 1 : 0
  }

  this.vendorService.getVendorByApartmentIdStatus(params).subscribe((res: any) => {

    this.allVendorListData = res
    this.isVendorLoaded = true;
    this.selectReport(this.pageName);
    },
    error => {

    });

  let vendorParams = {
    LookupTypeId: 57,
    ApartmentId: this.sessionService.apartmentId
  }

  this.lookupService.getLookupValueByLookupTypeId(vendorParams).subscribe((res: any) => {

    this.vendorCategoryData = res.filter(item => {
      return item.isActive;
    });
  });

    }
  }

  function viewVendor(row) {
    var event = new CustomEvent('onViewVendor', {
      detail: {
        rowId: row
      }
    })
    window.dispatchEvent(event);
  }
  (window as any).viewVendor = viewVendor;


