import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { VendorReportsComponent } from './vendor-reports.component';
import { VendorReportDataComponent } from './vendor-report-data/vendor-report-data.component';

export const routes: Routes = [
  { path     : '', component: VendorReportsComponent },
  { path: ':name', component: VendorReportDataComponent, canActivate: [AuthGuard] },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class VendorReportsRoutingModule { }
